@font-face {
  font-family: Outfit;
  src: url(./assets/family/Outfit-VariableFont_wght.ttf);
}
@font-face {
  font-family: KeaniaOne;
  src: url(./assets/family/KeaniaOne-Regular.ttf);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Outfit, sans-serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.app {
  background-color: #0D0D12;
  min-height: 100vh;
  width: 100%;
  padding-bottom: 50px;
}

.qr {
  height: 100vh;
}
.qr img {
  width: 50%;
}

.header-inner {
  padding: 20px;
  margin: 0px 0 20px;
}
.header__title {
  font-family: KeaniaOne;
  color: #41A4DF;
  text-transform: uppercase;
  font-size: 64px;
  text-decoration: none;
}

.list {
  justify-content: flex-start;
  padding: 0 20px;
  color: #fff;
  margin-bottom: 40px;
}
.list-inner {
  justify-content: flex-start;
  gap: 20px;
}
.list__title {
  font-size: 32px;
  font-weight: 900;
  margin-bottom: 40px;
}
.list__title span {
  font-size: 32px;
  font-weight: 900;
  color: #41A4DF;
}

.card {
  padding: 20px;
  border: 2px solid #ffffff;
  border-radius: 16px;
  width: 50%;
  height: 200px;
  text-decoration: none;
  color: #ffffff;
  transition: 0.2s;
}
.card:hover {
  cursor: pointer;
  background-color: #41A4DF;
  border: 2px solid #41A4DF;
}
.card__title {
  font-size: 28px;
}

.content {
  color: #ffffff;
  height: calc(100vh - 138px);
  flex-direction: column;
  justify-content: center;
}
.content-inner {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 50px;
  padding: 0 20px;
}
.content__title {
  font-size: 64px;
}
.content__title span {
  opacity: 0.5;
}
.content__btn {
  width: 100%;
  padding: 40px 20px;
  border: 2px solid #41A4DF;
  background-color: #41A4DF;
  color: #ffffff;
  font-size: 32px;
  border-radius: 20px;
  transition: 0.2s;
}
.content__btn:hover {
  cursor: pointer;
  background-color: #41A4DF;
}
.content__btn.help {
  background: none;
  padding: 20px;
  justify-content: space-between;
  text-align: center;
  gap: 10px;
}
.content__btn.help h2 {
  font-size: 18px;
}
.content__btn.help button {
  padding: 5px 10px;
  border: 2px solid #41A4DF;
  background: #41A4DF;
  color: #ffffff;
  font-size: 16px;
  border-radius: 10px;
  transition: 0.2s;
}
.content__btn.help button:hover {
  cursor: pointer;
}
.content__input {
  width: 100%;
  padding: 20px 20px;
  border: 2px solid #41A4DF;
  background: none;
  color: #ffffff;
  font-size: 32px;
  border-radius: 20px;
  transition: 0.2s;
}
.content__input:hover {
  cursor: pointer;
}

@media screen and (max-width: 1000px) {
  .card__title {
    font-size: 3vw;
  }
  .content__title {
    font-size: 10vw;
  }
}
@media screen and (max-width: 600px) {
  .list__title {
    font-size: 4vw;
  }
  .card {
    height: 100px;
  }
}

